@import '../../../variables/variables.scss';

.root-header {
	width: 100%;
}

.root-header__wrap {
	width: 100%;
	height: min-content;

	display: grid;
	grid-template-columns: 1fr min-content;
	grid-template-rows: repeat(2, min-content);
	grid-row-gap: 30px;
	padding: 0 $standart-side-padding;

	& .root-navigation {
		grid-row-start: 2;
	}

	& .root-exit-button {
		grid-row-start: 1;
		grid-column-start: 2;
	}
}
